.card {
    position: relative;
    margin: 1rem;
    padding-bottom: calc(2em + 60px);
    background: white;
    box-shadow: 2px 4px 25px rgba(0, 0, 0, .1);
    border-radius: 12px;
    overflow: hidden;
    transition: all 0.3s linear;
  }
  
  .card h1 {
    text-align: center;
    padding: 1.5rem 2.5rem;
    background-image: linear-gradient(120deg, #fbc2eb 0%, #a6c1ee 100%);
    margin: 0 0 2rem 0;
    font-size: 1.5rem;
    color: white;
  }
  
  .card p {
    padding: 0 2em;
    margin: 0;
  }
  
  .card button {
    position: absolute;
    bottom: 0;
    left: 10%;
    right: 10%;
    width: 80%;
    display: block;
    margin: 1em 0;
    height: 60px;
    background-color: transparent;
    border: 2px solid #666;
    border-radius: 12px;
    color: #666;
    cursor: pointer;
    transition: all 0.2s linear;
  }
  