label.logo {
  color: white;
  font-size: 35px;
  float: left;
  padding-left: 1%;
}


.topnav {
  background-color: rgb(68, 68, 68);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5%;
}

.topnav ul {
  display: flex;
  text-decoration: none;
}

.topnav ul li {
  display: block;
  margin-left: 25px;
}

.topnav ul li a {
  color: rgb(45, 45, 45);
  padding: 10px;
  background-color: rgb(255, 255, 255);
  border-color: rgb(255, 255, 255);
  border-style: solid;
  border-radius: 25px;
  border-width: 5px 25px 5px 25px;
  text-decoration: none;
}

.topnav ul li a.active {
  border:none;
  font-weight: bold;
  text-decoration: underline;
}

.topnav .icon {
  display: none;
}

@media only screen and (max-width: 1385px) {
  .topnav ul {
    display: flex;
    float: left;
    position: fixed;
    top: 40px;
    padding-top: 20px;
    flex-direction: column;
    background-color: #fff;
    width: 50%;
    border-radius: 10px;
  }

  .topnav ul li {
    margin: 10px;
    background-color: blue;
  }

  .topnav ul li a {
    border-style: hidden;
  }

  .topnav ul li a.active {
    left:0;
  }

  .topnav active {
    left: 0;
  }

  .hamburger {
    display: inline-block;
    cursor: pointer;
  }

}