body {
  background-color: #6b9fe8;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}

label.logo {
  color: #fff;
  float: left;
  padding-left: 1%;
  font-size: 35px;
}

.topnav {
  background-color: #444;
  justify-content: space-between;
  align-items: center;
  padding: 1.5%;
  display: flex;
}

.topnav ul {
  text-decoration: none;
  display: flex;
}

.topnav ul li {
  margin-left: 25px;
  display: block;
}

.topnav ul li a {
  color: #2d2d2d;
  background-color: #fff;
  border: 5px solid #fff;
  border-width: 5px 25px;
  border-radius: 25px;
  padding: 10px;
  text-decoration: none;
}

.topnav ul li a.active {
  border: none;
  font-weight: bold;
  text-decoration: underline;
}

.topnav .icon {
  display: none;
}

@media only screen and (max-width: 1385px) {
  .topnav ul {
    float: left;
    width: 50%;
    background-color: #fff;
    border-radius: 10px;
    flex-direction: column;
    padding-top: 20px;
    display: flex;
    position: fixed;
    top: 40px;
  }

  .topnav ul li {
    background-color: #00f;
    margin: 10px;
  }

  .topnav ul li a {
    border-style: hidden;
  }

  .topnav ul li a.active, .topnav active {
    left: 0;
  }

  .hamburger {
    cursor: pointer;
    display: inline-block;
  }
}

h1 {
  text-align: center;
  margin: 0;
  padding: 0 64px;
  font-family: Montserrat, sans-serif;
  font-size: 48px;
  font-weight: 200;
  line-height: 59px;
}

.stopwatch {
  grid-row-gap: 23px;
  width: 100%;
  justify-items: center;
  margin-top: 10%;
  padding-top: 25px;
  display: grid;
}

.time {
  background-color: #fff;
  border: 5px solid #fff;
  border-width: 5px 40px;
  border-radius: 25px;
  font-family: Roboto Mono, monospace;
  font-size: 48px;
  font-weight: 300;
}

.controls {
  width: 187px;
  justify-content: space-between;
  display: flex;
}

button {
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
  margin: 0;
  padding: 0;
}

.timelabel {
  color: #fff;
  text-align: center;
}

#playButton {
  display: block;
}

#pauseButton {
  display: none;
}

#task-grid {
  grid-template: "taskform tasklist" 1fr
  / 1fr 1fr;
  display: grid;
}

#taskform {
  max-width: 300px;
  grid-area: taskform;
  margin: 0 auto;
}

#taskform label {
  width: 100%;
  display: block;
}

#tasklist {
  grid-area: tasklist;
  list-style-type: none;
}

.card {
  background: #fff;
  border-radius: 12px;
  margin: 1rem;
  padding-bottom: calc(2em + 60px);
  transition: all .3s linear;
  position: relative;
  overflow: hidden;
  box-shadow: 2px 4px 25px #0000001a;
}

.card h1 {
  text-align: center;
  color: #fff;
  background-image: linear-gradient(120deg, #fbc2eb 0%, #a6c1ee 100%);
  margin: 0 0 2rem;
  padding: 1.5rem 2.5rem;
  font-size: 1.5rem;
}

.card p {
  margin: 0;
  padding: 0 2em;
}

.card button {
  width: 80%;
  height: 60px;
  color: #666;
  cursor: pointer;
  background-color: #0000;
  border: 2px solid #666;
  border-radius: 12px;
  margin: 1em 0;
  transition: all .2s linear;
  display: block;
  position: absolute;
  bottom: 0;
  left: 10%;
  right: 10%;
}

